import React, { Component } from 'react';
import FormUser from './FormRegister';
import { CreateUser } from './CreateUser';

class Register extends Component {

    handleSubmit = (data) =>{
        console.log('enviado');
        CreateUser(data);
    };

    render() {
        return (
            <div>
                <FormUser onSubmit={this.handleSubmit} />
            </div>
        );
    }
}

export default Register;




