import React, { Component } from 'react';
import { FormGroup, Form, Button, Label, Input, FormText, FormFeedback } from 'reactstrap';
import { CreateUser } from "./CreateUser";


class FormRegister extends Component{

    constructor(){
        super();
        console.info(process.env.REST_API_LOCATION);
        this.state = this.getInitialState();
    }

    getInitialState = () => {
        return {
            user: {
                content: '',
                validate: true,
            },

            email: {
                content: '',
                validate: true,
            },

            password: {
                content: '',
                validate: true,
            }
        }
    };

    getUser = (info) =>{
        return fetch('http://127.0.0.1:8000/api/v1/users/?search='+info)
        .then(resolve => {
            return resolve.json();
        }).then(data =>{
            return Object.keys(data)[0];
        });
    };

    checkUser = (value, target) =>{
        if (value === undefined){
            this.setState({
                user:{
                    content: target,
                    validate: true
                }
            });
        }else{
            this.setState({
                user:{
                    content: target,
                    validate: false
                }
            });
        }
    };

    checkEmail = (value, target) =>{
        if (value === undefined){
            this.setState({
                email:{
                    content: target,
                    validate: true
                }
            });
        }else{
            this.setState({
                email:{
                    content: target,
                    validate: false
                }
            });
        }
    };

    handleUserChange = (e) => {
        var target = e.target.value;
        var username=this.getUser(target);

        username.then(info =>{
            this.checkUser(info, target);
        });

        if (e.target.value.length === 0){
            this.setState({
                user:{
                    validate: false,
                    content: e.target.value
                }
            });
        }else{
            this.setState({
                user:{
                    validate: true,
                    content: e.target.value
                }
            });
        }
    };

    handleEmailChange = (e) => {
        var target = e.target.value;
        var email=this.getUser(target);

        email.then(info =>{
            this.checkEmail(info, target);
        });

        let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

        if(re.test(e.target.value) && e.target.value!==0){
            this.setState({
                email:{
                    content: e.target.value,
                    validate: true
                }
            });
        }else{
            this.setState({
                email:{
                    content: e.target.value,
                    validate: false
                }
            });
        };
    };

    handlePasswordChange = (e) => {
        if(e.target.value!==0){
            this.setState({
                password:{
                    content: e.target.value,
                    validate: true
                }
            });
        }else{
            this.setState({
                password:{
                    content: e.target.value,
                    validate: false
                }
            });
        }
    };

    handleSubmit = (e) =>{
        e.preventDefault();
        if(this.state.user.validate === true && this.state.email.validate === true && this.state.password.validate === true){
            let data = { username: this.state.user.content, email: this.state.email.content,password: this.state.password.content};
           CreateUser(data);
            //this.setState(this.getInitialState());
        }else{
            console.log(this.state);
        }
    };

    render() {
        return (
            <Form className="form-horizontal" onSubmit={this.handleSubmit}>
                <FormGroup>
                    <Label for="user">Usuario</Label>
                    <Input  type="text"
                           id="user"
                           required="required"
                           value={this.state.user.content}
                           onChange={this.handleUserChange}
                           className={this.state.user.validate ? 'valid' : 'is-invalid'}
                           placeholder="Usuario"/>
                    <FormFeedback tooltip>El usuario ya existe</FormFeedback>
                </FormGroup>
                <FormGroup>
                    <Label for="email">Email</Label>
                    <Input  type="email"
                            id="email"
                            required="required"
                            value={this.state.email.content}
                            onChange={this.handleEmailChange}
                            className={this.state.email.validate ? 'valid' : 'is-invalid'}
                            placeholder="Email"/>
                    <FormFeedback tooltip>Escribe un email válido que no este en uso</FormFeedback>
                </FormGroup>
                <FormGroup>
                    <Label for="password">Password</Label>
                    <Input  type="password"
                            id="password"
                            required="required"
                            value={this.state.password.content}
                            onChange={this.handlePasswordChange}
                            className={this.state.password.validate ? 'valid' : 'is-invalid'}
                            placeholder="Email"/>
                    <FormFeedback tooltip>Campo obligatorio</FormFeedback>
                </FormGroup>

                <Button type="submit">Sign in</Button>
            </Form>
        )
    }
}

export default FormRegister;


